    }

.newTrade_app__2v4bn table td:first-child, .newTrade_app__2v4bn table th:first-child {
  border-left: 1px #f2ddb1 solid;
  border-left: 1px #f2ddb1 solid;
  border-left: 1px var(--border-color) solid;
}

.newTrade_app__2v4bn table td:last-child, .newTrade_app__2v4bn table th:last-child {
  border-right: 1px #f2ddb1 solid;
